.service-card {
	display: block;
	border-radius: 11px;
	padding: 20px 10px 15px;
	width: 100%;
	min-height: 140px;
	background-color: #F5F5F5;
	text-decoration: none;
	color: var(--card-color);
	transition: all .15s ease;
	transform: scale3d(1, 1, 1);

	@media (min-width: 576px) {
		padding: 18px 14px 18px;
	}

	@media (min-width: 992px) {
		padding: 18px 12px 18px;
	}

	&:hover {
		//box-shadow: 2px 2px 10px -4px rgba(0,0,0,0.5);
		color: var(--primary-color);
		transform: scale3d(1.06,1.06,1);
		background-color: var(--card-bg-hover) !important;
		
		.service-card__title {
			color: #fff;
		}
	}

	.service-card__title {
		display: flex;
		justify-content: center;
		flex-direction: column;
		margin: 10px 0 0;
		line-height: 1.13em;
		height: 40px;
		transition: all .15s ease;

		@media (min-width: 576px) {
			height: 42px;
		}

		@media (min-width: 768px) {
			height: 32px;
		}

		@media (min-width: 992px) {
			max-width: 80%;
			margin-left: auto;
			margin-right: auto;
		}
	}

	span {
		display: block;
		font-size: 11px;
		text-align: center;
		word-wrap: break-word;

		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
		line-height: 1.13em;

		overflow: hidden;
		position: relative;
		max-width: 100%;

		@media (min-width: 576px) {
			font-size: 12px;
		}

		@media (min-width: 768px) {
			font-size: 13px;
			-webkit-line-clamp: 2;
		}
	}

	&__image {
		max-width: 95px;
		max-height: fit-content;
		margin: auto;

		@media (min-width: 992px) {
			max-width: 120px;
		}
	}

	&.circled {
		padding: 5px;
		height: 140px;
		background-color: transparent !important;

		@media (min-width: 370px) {
			height: 150px;
		}

		@media (min-width: 576px) {
			height: 156px;
		}

		@media (min-width: 768px) {
			height: 174px;
		}

		@media (min-width: 992px) {
			height: 198px;
			padding: 10px 15px;
		}

		&:hover {
			box-shadow: none;
		}

		.service-card__image {
			max-width: fit-content;
			max-height: unset;
			margin: 0 auto 5px;

			@media (min-width: 992px) {
				margin: 0 auto 10px;
			}
		}

		img {
			border-radius: 50%;
			max-width: 75px;
			max-height: 75px;
			min-width: 75px;
			min-height: 75px;
			box-shadow: 0 20px 41px 0 rgba(79, 34, 107, 0.12);
			border: 4px solid #fff;

			@media (min-width: 360px) {
				max-width: 92px;
				max-height: 92px;
				min-width: 92px;
				min-height: 92px;
			}

			@media (min-width: 576px) {
				max-width: 96px;
				max-height: 96px;
				min-width: 96px;
				min-height: 96px;
			}

			@media (min-width: 768px) {
				max-width: 110px;
				max-height: 110px;
				min-width: 110px;
				min-height: 110px;
				box-shadow: 0 7px 12px 0 rgba(79, 34, 107, 0.12);
			}

			@media (min-width: 992px) {

				//box-shadow: 0 20px 41px 0 rgba(79, 34, 107, 0.12);
			}

			@media (min-width: 992px) {
				//box-shadow: 0 20px 30px 0 rgba(79, 34, 107, 0.12);
			}

			@media (min-width: 1200px) {
				//box-shadow: 0 20px 41px 0 rgba(79, 34, 107, 0.12);
			}
		}

		span {
			max-width: 118px;
			margin-left: auto;
			margin-right: auto;

			@media (min-width: 576px) {
				max-width: 140px;
			}

			@media (min-width: 768px) {
				-webkit-line-clamp: 3;
			}

			@media (min-width: 768px) {
				max-width: 160px;
			}
		}
	}
}