.category-card {
	display: flex;
	border-radius: 23px;
	padding: 12px;
	background-repeat: no-repeat;
	background-position: bottom left -42px;
	background-size: cover;
	width: 100%;
	height: clamp(135px, 38vw, 230px);
	overflow: hidden;
	text-decoration: none;
	color: var(--card-color);
	font-size: 12px;
	font-weight: 600;
	line-height: 1.2em;
	flex-direction: column;
	justify-content: flex-end;
	text-align: right;
	position: relative;
	transition: all 0.15s ease;
	//transform: scale3d(1, 1, 1);

	@media (min-width: 470px) {
		height: clamp(140px, 28vw, 185px);
	}

	@media (min-width: 576px) {
		height: 160px;
		background-size: 182px;
		justify-content: flex-end;
	}

	@media (min-width: 768px) {
		font-size: 14px;
		background-size: calc(32px + 20vw);
		height: clamp(160px, 20vw, 210px);
	}

	@media (min-width: 992px) {
		font-size: 16px;
		height: 185px;
		background-size: 220px;
		padding: 20px;
		background-position: bottom left -50px;
	}

	@media (min-width: 1400px) {
		//text-align: left;
	}

	&:hover {
		//box-shadow: 0 0 11px 2px rgba(0, 0, 0, 0.2);
		color: var(--primary-color);
		//border-color: #e8e8e8;
		transform: translateY(-4px) translateX(-4px);
		//transform: scale3d(1.01,1.01,1);

		span {
			//background-color: rgba(255,255,255,1);
			//box-shadow: inset 2px 2px 2px 0 #cbcbcb;
		}
	}

	&::before {
		position: absolute;
		//content: "";
		bottom: -55px;
		left: -20%;
		width: 140%;
		height: 70%;
		filter: blur(25px);
		background-color: inherit;

		@media (min-width: 470px) {
			bottom: -46px;
		}

		@media (min-width: 576px) {
			bottom: -44px;
		}

		@media (min-width: 768px) {
			bottom: -66px;
			height: 75%;
			filter: blur(40px);
		}

		@media (min-width: 992px) {
			bottom: -55px;
			filter: blur(55px);
		}

		@media (min-width: 1200px) {
			filter: blur(65px);
			bottom: -70px;
			opacity: 0.5;
		}
	}

	&.mixed-color {
		&[data-type="build"] {
			background-color: #babaee !important;
		}

		&[data-type="fix"] {
			background-color: #c6e89a !important;
		}

		&[data-type="cosmetic-repair"] {
			background-color: #faefd8 !important;
		}

		&[data-type="beauty"] {
			background-color: #ffdaad !important;
		}

		&[data-type="cleaning"] {
			background-color: #95e2df !important;
		}

		&[data-type="delivery"] {
			background-color: #f4b9bb !important;
		}

		&[data-type="pc-help"] {
			background-color: #f2f2f2 !important;
		}
	}

	&.gray-color {
		background-color: #f1f1f1;
	}

	span {
		display: block;
		position: relative;
		z-index: 1;
		max-width: 315px;
		margin-left: auto;

		padding: 6px 15px;
		border-radius: 30px;
		background-color: rgba(255,255,255,1);
		transition: all 0.15s ease;


		@media (min-width: 576px) {
			max-width: 250px;
		}

		@media (min-width: 768px) {
			margin-right: 0;
			max-width: 148px;
		}

		@media (min-width: 1200px) {
			max-width: 210px;
		}

		@media (min-width: 1400px) {
			//max-width: 190px;
			//margin-left: clamp(20%, 40vw, 45%);
			//margin-right: 0;

			//max-width: 125px;
			//margin-left: auto;
		}

		@media (min-width: 1650px) {
			//max-width: 190px;
			//margin-left: clamp(20%, 40vw, 45%);
		}
	}
}

.category-card__underplate {
	background-color: #cbcbcb;
	position: absolute;
	width: calc(100% - 2px);
	height: calc(100% - 2px);
	top: 1px;
	left: 1px;
	border-radius: 23px;
}