.footer {
  margin-top: 30px;
  background: var(--footer-bg-color);
  color: var(--footer-text-color);
  padding: 20px 10px;
  font-size: 14px;

  @media (min-width: 768px) {
    font-size: 16px;
  }

  @media (min-width: 992px) {
    padding: 53px 43px;
  }

  @media (min-width: 1200px) {
    font-size: 18px;
  }

  @media (min-width: 1650px) {
    font-size: 20px;
  }

  a {
    color: inherit;
    text-decoration: none;
    line-height: 100%;

    &:hover {
      color: var(--primary-color);
      text-decoration: underline;
    }
  }
}

.footer__content {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  position: relative;

  & > .row {
    width: 100%;
    flex-grow: 1;

    @media (min-width: 992px) {
      width: 85%;
    }
  }
}

.footer__links {
  li {
    font-size: 13px;
    line-height: 130%;

    @media (min-width: 768px) {
      font-size: 14px;
    }

    @media (min-width: 992px) {
      line-height: 150%;
    }

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
}

.footer__soc {
  position: absolute;
  right: clamp(5px, 5vw, 55px);
  bottom: 105px;

  @media (min-width: 470px) {
    right: auto;
    left: calc(50% + 10px);
  }

  @media (min-width: 768px) {
    position: static;
    order: 2;
    margin-left: auto;
  }

  @media (min-width: 992px) {
    position: static;
  }

  @media (min-width: 1200px) {
    order: 1;
  }
}

.footer__copy {
  margin-top: 10px;
  width: 100%;
  font-weight: 700;
  font-size: 18px;
  text-align: center;

  @media (min-width: 768px) {
    font-size: 20px;
    width: 85%;
    order: 1;
    text-align: left;
  }

  @media (min-width: 992px) {
    margin-top: 30px;
  }

  @media (min-width: 1200px) {
    width: 100%;
    order: 2;
  }
}