.services {
	.section-title {
		position: relative;
		z-index: 2;

		@media (min-width: 768px) {
			margin-bottom: 4px;
		}
	}

	.swiper {
		width: 100%;
		margin-left: auto;
		margin-right: auto;
		height: 332px;
		border-radius: 11px;
		margin-bottom: -10px;
		padding: 10px 20px;

		@media (min-width: 360px) {
			height: 326px;
		}

		@media (min-width: 576px) {
			height: 346px;
			padding: 20px 20px 10px;
		}

		@media (min-width: 768px) {
			height: 332px;
			padding: 20px 0 10px;
		}

		@media (min-width: 992px) {
			height: 358px;
		}
	}

	.swiper-wrapper {
		display: flex;
		justify-content: flex-start;
	}

	.swiper-slide {
		text-align: center;
		height: calc((100% - 22px) / 2) !important;

		display: flex;
		justify-content: center;
		align-items: center;
	}

	.swiper-button-next {
		@media (min-width: 1650px) {
			//right: var(--swiper-navigation-sides-offset, -30px);
		}

		@media (min-width: 1832px) {
			//right: var(--swiper-navigation-sides-offset, -60px);
		}
	}

	.swiper-button-prev {
		@media (min-width: 1650px) {
			//left: var(--swiper-navigation-sides-offset, -30px);
		}

		@media (min-width: 1832px) {
			//left: var(--swiper-navigation-sides-offset, -60px);
		}
	}

	&--circled {
		.swiper {
			height: 295px;
			border-radius: 60px;

			@media (min-width: 360px) {
				height: 325px;
			}

			@media (min-width: 576px) {
				height: 345px;
			}

			@media (min-width: 768px) {
				height: 376px;
				padding-bottom: 0;
			}

			@media (min-width: 992px) {
				height: 198px;
				padding: 0;
			}
		}

		.swiper-wrapper {
			justify-content: unset;

		}

		.services__slider-wrap {
			padding-bottom: 20px;

			@media (min-width: 768px) {
				padding-bottom: 0;
			}

			@media (min-width: 992px) {
				padding: 0 50px;
			}

			@media (min-width: 1200px) {
				padding: 0 60px;
			}
		}

		.swiper-slide {
			height: calc((100% - 30px) / 2) !important;

			@media (min-width: 992px) {
				height: 100% !important;
				margin-left: auto;
				margin-right: auto;
			}
		}

		.swiper-button-next, .swiper-button-prev {
			@media (min-width: 768px) {
				top: var(--swiper-navigation-top-offset, 44%);
			}

			@media (min-width: 992px) {
				top: var(--swiper-navigation-top-offset, 35%);
			}
		}

		.swiper-button-next {
			@media (min-width: 1650px) {
				right: var(--swiper-navigation-sides-offset, -10px);
			}


		}

		.swiper-button-prev {
			@media (min-width: 1650px) {
				left: var(--swiper-navigation-sides-offset, -10px);
			}


		}
	}
}

.services__slider-wrap {
	position: relative;
	border-radius: 11px;
	padding-bottom: 40px;
	margin: -10px;

	@media (min-width: 576px) {
		margin: 0 -20px;
	}

	@media (min-width: 768px) {
		padding: 0 60px;
	}

	@media (min-width: 1650px) {
		padding: 0;
	}
}
