.categories {
	&--varial-width {
		.categories__item {

			span {
				padding: 6px 15px;
				border-radius: 30px;
				background-color: rgba(255,255,255,0.9);

				@media (min-width: 576px) {
					max-width: 160px;
					text-shadow: 0 0 6px rgba(255, 255, 255, 1);
				}

				@media (min-width: 200px) {
					max-width: 200px;
				}

				@media (min-width: 1200px) {
					max-width: 300px;
				}
			}

			&:nth-child(12n - 11) {
				@media (min-width: 576px) {
					width: calc(24% - 5px);
				}
			}

			&:nth-child(12n - 10) {
				@media (min-width: 576px) {
					width: calc(32.5% - 5px);
				}
			}

			&:nth-child(12n - 9) {
				@media (min-width: 576px) {
					width: calc(40.9% - 5px);
				}
			}

			&:nth-child(12n - 8) {
				@media (min-width: 576px) {
					width: calc(100% / 3 - 15px);
				}
			}

			&:nth-child(12n - 7) {
				@media (min-width: 576px) {
					width: calc(100% / 3 - 15px);
				}
			}

			&:nth-child(12n - 6) {
				@media (min-width: 576px) {
					width: calc(100% / 3 - 15px);
				}
			}

			&:nth-child(12n - 5) {
				@media (min-width: 576px) {
					width: calc(24% - 5px);
				}
			}

			&:nth-child(12n - 4) {
				@media (min-width: 576px) {
					width: calc(49.3% - 5px);
				}
			}

			&:nth-child(12n - 3) {
				@media (min-width: 576px) {
					width: calc(24% - 5px);
				}
			}

			&:nth-child(12n - 2) {
				@media (min-width: 576px) {
					width: calc(100% / 3 - 15px);
				}
			}

			&:nth-child(12n - 1) {
				@media (min-width: 576px) {
					width: calc(100% / 3 - 15px);
				}
			}

			&:nth-child(12n) {
				@media (min-width: 576px) {
					width: calc(100% / 3 - 15px);
				}
			}
		}

		.category-card {
			@media (min-width: 576px) {
				height: 160px;
				background-position: bottom left -15px;
			}

			@media (min-width: 768px) {
				background-size: 185px;
			}
		}
	}

	&--varial-width_4 {
		.categories__item {

			span {
				@media (min-width: 576px) {
					max-width: 160px;
				}

				@media (min-width: 200px) {
					max-width: 200px;
				}

				@media (min-width: 1200px) {
					max-width: 300px;
				}
			}

			&:nth-child(12n - 11) {
				@media (min-width: 576px) {
					width: calc(28% - 5px);
				}
			}

			&:nth-child(12n - 10) {
				@media (min-width: 576px) {
					width: calc(30% - 5px);
				}
			}

			&:nth-child(12n - 9) {
				@media (min-width: 576px) {
					width: calc(40% - 5px);
				}
			}

			&:nth-child(12n - 8) {
				@media (min-width: 576px) {
					width: calc(100% / 3 - 15px);
				}
			}

			&:nth-child(12n - 7) {
				@media (min-width: 576px) {
					width: calc(100% / 3 - 15px);
				}
			}

			&:nth-child(12n - 6) {
				@media (min-width: 576px) {
					width: calc(100% / 3 - 15px);
				}
			}

			&:nth-child(12n - 5) {
				@media (min-width: 576px) {
					width: calc(28% - 5px);
				}
			}

			&:nth-child(12n - 4) {
				@media (min-width: 576px) {
					width: calc(40% - 5px);
				}
			}

			&:nth-child(12n - 3) {
				@media (min-width: 576px) {
					width: calc(28% - 5px);
				}
			}

			&:nth-child(12n - 2) {
				@media (min-width: 576px) {
					width: calc(100% / 3 - 15px);
				}
			}

			&:nth-child(12n - 1) {
				@media (min-width: 576px) {
					width: calc(100% / 3 - 15px);
				}
			}

			&:nth-child(12n) {
				@media (min-width: 576px) {
					width: calc(100% / 3 - 15px);
				}
			}
		}

		.category-card {
			@media (min-width: 576px) {
				height: 160px;
				background-position: bottom left -15px;
			}

			@media (min-width: 768px) {
				background-size: 185px;
			}
		}
	}
}

.categories__list {
	display: flex;
	flex-wrap: wrap;
	gap: 5px;

	@media (min-width: 768px) {
		margin-bottom: 0;
	}

	@media (min-width: 992px) {
		gap: 10px;
	}
}

.categories__item {
	position: relative;
	width: calc(50% - 3.5px);

	@media (min-width: 470px) {
		width: calc(100% / 3 - 4.5px);
		max-width: calc(100% / 2 - 4.5px);
		flex-grow: 1;
	}

	@media (min-width: 576px) {
		width: calc(100% / 4 - 4.5px);
	}

	@media (min-width: 992px) {
		width: calc(100% / 5 - 8px);
	}
}