.header {
  width: 100%;
  transition: background-color var(--bs-transition);
  z-index: 50;

  &.header-is-fixed {
    position: fixed;
    background-color: #fff;
    transition: all .15s ease;

    &.scroll {
      box-shadow: 0 1px 9px -4px rgba(0, 0, 0, 0.5);
      transition: all .3s ease;
    }

    & + main {
      padding-top: 60px;

      @media (min-width: 768px) {
        padding-top: 62px;
      }

      @media (min-width: 992px) {
        padding-top: 72px;
      }
    }
  }
}

.header .navbar__content {
  display: flex;
  flex-wrap: wrap;
  width: 100%;

  @media (min-width: 992px) {
    width: fit-content;
  }

  @media (min-width: 1200px) {
    margin-right: 20px;
  }
}

.navbar-brand {
  width: 45%;
  margin-right: 0;

  @media (min-width: 992px) {
    width: 200px;
  }

  @media (min-width: 1200px) {
    width: fit-content;
  }

  img, svg {
    max-width: 100%;
  }
}

.header .navbar-toggler {
  margin-left: auto;
}
  // Burger
.navbar-toggler {
  border: none !important;
  padding: 10px;
  position: relative;
  right: -10px;

  &:focus {
    border: none !important;
    box-shadow: none;
  }

  .toggler-icon {
    width: 28px;
    height: 4px;
    border-radius: 4px;
    background-color: var(--primary-color);
    background-color: #1db5e6;
    display: block;
    transition: all 0.2s;
    position: relative;
  }

  .top-bar {
    transform: rotate(0);
  }

  .middle-bar {
    opacity: 1;
    filter: alpha(opacity=100);
    margin: 6px auto;
  }

  .bottom-bar {
    transform: rotate(0);
  }
}

.navbar-toggler {
  &[aria-expanded="true"] {
    .toggler-icon {
      //background-color: #fff;
      //width: 37px;
    }

    .top-bar {
      transform: translateX(25%) rotate(45deg);
      transform-origin: 10% 10%;
      top: 2.5px;
    }

    .middle-bar {
      opacity: 0;
      filter: alpha(opacity=0);
    }

    .bottom-bar {
      transform: translateX(25%) rotate(-45deg);
      transform-origin: 10% 90%;
      bottom: 2.5px;
    }
  }
}

.header {
  .navbar {
    @media (min-width: 992px) {
      padding-top: 10px;
      padding-bottom: 10px;
    }
  }

  .offcanvas {
    .btn-close {
      background-color: #14c4ff !important;
      filter: invert(1);
      opacity: 1;
    }

    .btn {
      width: 100%;
    }
  }

  .nav__menu {
    @media (min-width: 992px) {
      display: flex;
      justify-content: space-evenly;
      flex-grow: 1;
    }

    a {
      color: #3c4c53;
      text-decoration: none;

      @media (min-width: 992px) {
        font-size: 16px;
        font-weight: 700;
      }

      &:hover {
        @media (min-width: 992px) {
          color: var(--primary-color);
        }
      }
    }

    li {
      &:hover {
        @media (max-width: 991px) {
          background-color: var(--bs-light-gray);
        }
      }
    }
  }

  .navbar-brand svg {
    @media (min-width: 992px) {
      position: relative;
      top: -2px;
    }
  }
}

.header__btn {
  @media (min-width: 1200px) {
    margin-left: 20px;
  }
}
