.main-hero {
	& + .services {
		margin-top: -130px;
		margin-bottom: 30px;

		@media (min-width: 576px) {
			margin-bottom: 20px;
		}

		@media (min-width: 768px) {
			margin-bottom: 0;
		}

		@media (min-width: 992px) {
			margin-top: -175px;
		}
	}

	& + .announcement-map {
		margin-top: -130px;
		margin-bottom: 30px;

		@media (min-width: 576px) {
			margin-bottom: 20px;
		}

		@media (min-width: 768px) {
			margin-bottom: 0;
		}

		@media (min-width: 992px) {
			margin-top: -175px;
		}
	}

	&--with-image {
		.main-hero__plate {
			border-radius: 0;
			position: relative;
			padding: 30px 25px 90px;

			@media (min-width: 768px) {
				padding: 30px 25px 95px;
			}

			@media (min-width: 992px) {
				padding: 50px 25px 180px;
			}

			@media (min-width: 1200px) {
				padding: 100px 40px 140px;
			}

			& > * {
				position: relative;
				z-index: 2;
			}

			&::after {
			  position: absolute;
			  content: '';
			  bottom: 0;
			  right: 0;
			  width: 100%;
			  height: calc(100% - 30px);
				background-image: url('../img/bg/mian-hero-image.png');
				background-repeat: no-repeat;
				background-position: bottom -36px left calc(50% + 164px);
				background-size: 530px;

				@media (min-width: 768px) {
					background-size: 338px;
					background-position: bottom left calc(50% + 172px);
					transform: scale(-1, 1);
				}

				@media (min-width: 992px) {
					transform: scale(1);
					background-position: bottom left calc(50% + 176px);
					background-size: 440px;
				}

				@media (min-width: 1200px) {
					background-size: 687px;
					background-position: bottom right calc(50% - 366px);
					height: 100%;
				}

				@media (min-width: 1400px) {
					background-position: bottom right calc(50% - 400px);
				}
			}
		}

		.main-hero__form {
			@media (max-width: 575px) {
				max-width: 230px;
			}

			& > * {
				@media (min-width: 768px) {
					width: calc(50% - 12px);
				}

				&:nth-child(1) {
					@media (min-width: 992px) {
						width: calc(36% - 24px);
					}

					@media (min-width: 1200px) {
						width: calc(38% - 24px);
					}

					@media (min-width: 1400px) {
						width: 415px;
					}
				}

				&:nth-child(2) {
					@media (min-width: 992px) {
						width: calc(30% - 24px);
					}

					@media (min-width: 1200px) {
						width: calc(28% - 24px);
					}

					@media (min-width: 1400px) {
						width: 297px;
					}
				}

				&:nth-child(3) {
					@media (min-width: 992px) {
						width: calc(35% - 24px);
					}

					@media (min-width: 1200px) {
						width: calc(28% - 24px);
					}

					@media (min-width: 1400px) {
						width: 317px;
					}
				}
			}
		}

		& + .services {
			margin-top: -45px;
			margin-bottom: 10px;

			@media (min-width: 576px) {
				margin-top: -65px;
			}

			@media (min-width: 768px) {
				margin-bottom: 0;
			}

			@media (min-width: 992px) {
				margin-top: -72px;
			}
		}
	}
	
	&--inner {
		.section-title--main {
			@media (min-width: 360px) {
				max-width: 222px;
				font-size: 22px;
			}

			@media (min-width: 576px) {
				max-width: 60%;
				font-size: 25px;
			}

			@media (min-width: 768px) {
				max-width: 55%;
			}
		}

		.main-hero__plate {
			padding: 25px 15px 170px;

			@media (min-width: 576px) {
				padding: 25px 15px 200px;
			}

			@media (min-width: 768px) {
				padding: 25px 15px 160px;
			}

			@media (min-width: 992px) {
				padding: 90px 40px 230px;
			}

			@media (min-width: 1200px) {
				padding: 90px 40px 260px;
			}
		}

		.main-hero__form {
			max-width: 230px;

			@media (min-width: 576px) {
				max-width: 60%;
			}

			@media (min-width: 768px) {
				max-width: unset;
			}

			@media (min-width: 1200px) {
				max-width: 75%;
			}
		}
	}
}

.main-hero__plate {
	position: relative;
	background-repeat: no-repeat;
	background-position: center right -20px;
	background-size: cover;
	padding: 25px 15px 180px;
	border-radius: 20px;
	background-image: url('../img/bg/mian-hero.svg');

	@media (min-width: 768px) {
		background-position: center;
	}

	@media (min-width: 768px) and (max-width: 991px) {
		padding-bottom: 160px;
	}

	@media (min-width: 992px) {
		padding: 90px 40px 220px;
	}

	@media (min-width: 1200px) {
		border-radius: 40px;
	}

	& > .container {
		position: relative;
		z-index: 2;
	}
}

.main-hero__image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-repeat: no-repeat;
	background-position: center right;
	border-radius: 20px;
	opacity: 0;

	@media (min-width: 360px) {
		background-size: clamp(327px, 83vw, 429px);
		background-position: top 10px right -56px;
		opacity: 1;
	}

	@media (min-width: 470px) {
		background-position: top 10px right -10px;
	}

	@media (min-width: 768px) {
		background-position: top 20px right 45px;
		background-size: 355px;
	}

	@media (min-width: 992px) {
		background-position: top 20px right 45px;
		background-size: 450px;
	}

	@media (min-width: 1200px) {
		background-position: top 20px right 45px;
		background-size: 480px;
	}

	@media (min-width: 1400px) {
		background-position: top 20px right 45px;
		background-size: 580px;
	}
}

.main-hero__form {
	max-width: 320px;

	@media (min-width: 768px) {
		display: flex;
		flex-wrap: wrap;
		gap: 24px;
		max-width: unset;
		margin-top: 0;
	}

	@media (min-width: 992px) {
		align-items: flex-start;
	}

	.btn {
		@media (max-width: 767px) {
			width: 100%;
		}
	}

	.form-label {
		@media (min-width: 768px) {
			margin-bottom: 0;
		}
	}

	& > * {
		@media (min-width: 768px) {
			width: calc(50% - 12px);
		}

		&:nth-child(1) {
			@media (min-width: 992px) {
				width: calc(36% - 24px);
			}

			@media (min-width: 1200px) {
				width: calc(40% - 24px);
			}

			@media (min-width: 1400px) {
				width: 355px;
			}
		}

		&:nth-child(2) {
			@media (min-width: 992px) {
				width: calc(30% - 24px);
			}

			@media (min-width: 1200px) {
				width: calc(32% - 24px);
			}

			@media (min-width: 1400px) {
				width: 240px;
			}
		}

		&:nth-child(3) {
			@media (min-width: 992px) {
				width: calc(35% - 24px);
			}

			@media (min-width: 1200px) {
				width: calc(28% - 0px);
			}

			@media (min-width: 1400px) {
				width: 236px;
			}
		}
	}

	.form-text {
		font-size: 14px;
		line-height: 150%;

		@media (min-width: 768px) and (max-width: 991px) {
			padding-left: 18px;
		}

		@media (min-width: 992px) {
			text-align: center;
		}

		@media (min-width: 1200px) {
			text-align: left;
			font-size: 16px;
			margin-top: 12px;
		}
	}

	.btn {
		@media (min-width: 768px) and (max-width: 991px) {
			margin-left: auto;
			position: relative;
			top: -40px;
		}
	}
}

