$mainOffset: 15; // отступы безопасности для контейнера
$maxWidth: 1920; // максимальная ширина страницы
$minWidth: 320; // минимальная ширина телефона
$containerWidth: 1160; // ширина контейнера из макета
$maxWidthContainer: $containerWidth + ($mainOffset * 2); // ширина контейнера с учетом padding слева и справа

$headerHeight: 100;
$footerHeight: 100;



:root {
  --bs-font-family: Roboto, sans-serif; // Семейство шрифта по умолчанию
  --bs-font-family-second: Inter, sans-serif;
  --bs-font-size: 16px; // Размер шрифта по умолчанию
  --bs-font-weight: 500; // Жирность шрифта по умолчанию
  --bs-line-height: 175%; // Базовая высота шрифта
  --bs-body-bg: #fff;
  --bs-font-color: #000; // Цвет шрифта по умолчанию
  --bs-title-color: #263d6a; // Цвет заголовков

  @media (min-width: 768px) {
    --bs-font-size: 18px;
  }

  --primary-color: rgb(0, 69, 203);
  --primary-color-hover: rgb(16, 89, 230);

  --footer-bg-color: #f8f8f8;
  --footer-text-color: #000000;

  --card-color: #263d6a;
  --recommend-link-color: #55a8eb;

  --btn-text: #fff;
  --btn-bg: var(--primary-color);
  --btn-bg-hover: var(--primary-color-hover);

  --card-bg-hover: #5c84ec;

  --bs-light-gray: #f1f1f1;

}