.recommendation-card {
	color: #000;
	text-decoration: none;
	position: relative;

	&.favorite {
		.recommendation-card__favorite-icon {
			//background-image: url('../img/icons/heart-filled.svg');
		}
	}

	&:hover {
		.recommendation-card__image {
			img {
				transform: scale3d(1.05,1.05,1);
			}
		}

		.recommendation-card__title {
			color: var(--primary-color)
		}
	}
}

.recommendation-card__favorite-icon {
	position: absolute;
	top: 0;
	right: 0;
	width: 35px;
	height: 35px;
	background-image: url('../img/icons/heart.svg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: 20px;
	z-index: 2;
	filter: drop-shadow(0px 0px 7px rgba(0, 0, 0, 0.3));

	&.active {
		background-image: url('../img/icons/heart-filled.svg');
		filter: none;
	}
}

.recommendation-card__image {
	overflow: hidden;
	border-radius: 15px;

	img {
		max-width: 100%;
		max-height: 260px;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center;
		transition: all .2s ease;
		transform: scale3d(1,1,1);
	}
}

.recommendation-card__title {
	margin: 5px 0;
	font-size: 12px;
	color: var(--recommend-link-color);
	transition: all .2s ease;

	word-wrap: break-word;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 3;
	-webkit-box-orient: vertical;
	line-height: 1.13em;
	overflow: hidden;

	@media (min-width: 576px) {
		font-weight: 700;
	}

	@media (min-width: 768px) {
		font-size: 14px;
	}
}

.recommendation-card__price {
	line-height: 120%;
	color: inherit;
	display: inline-block;
	font-size: 14px;
	font-weight: 600;
	margin: 0 0 5px;

	@media (min-width: 576px) {
		font-size: 16px;
	}
}

.recommendation-card__location {
	opacity: 0.75;
	font-size: 12px;
	line-height: 120%;
	color: inherit;

	@media (min-width: 576px) {
		opacity: 0.8;
	}

	@media (min-width: 768px) {
		opacity: 1;
		font-weight: 300;
		font-size: 13px;
	}
}

.recommendation-card__period {
	opacity: 0.75;
	margin-top: 2px;
	font-size: 12px;
	color: inherit;
	line-height: 120%;

	@media (min-width: 576px) {
		opacity: 0.8;
	}

	@media (min-width: 768px) {
		opacity: 1;
		font-weight: 300;
		font-size: 13px;
	}
}