html {
  &.loaded {
    opacity: 1 !important;
  }
}

html,
body {
  min-width: 320px;
  width: 100%;
  font-family: var(--bs-font-family);
  background-color: var(--bs-body-bg);
  color: var(--bs-font-color);
  font-size: var(--bs-font-size);
  font-weight: var(--bs-font-weight);
  line-height: var(--bs-line-height);
}

.section-padding {
  padding: 15px 0;

  @media (min-width: 768px) {
    padding: 30px 0;
  }

  @media (min-width: 1200px) {
    padding: 45px 0;
  }
}

.section-margin {
  margin: 15px 0;

  @media (min-width: 768px) {
    margin: 30px 0;
  }

  @media (min-width: 1200px) {
    margin: 45px 0;
  }
}

.section-title {
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 10px;
  color: var(--bs-title-color);
  text-align: center;

  @media (min-width: 768px) {
    font-size: 30px;
    margin-bottom: 24px;
  }

  @media (min-width: 1650px) {
    text-align: left;
  }

  &--main {
    font-weight: 700;
    font-size: 25px;
    margin-bottom: 20px;
    text-align: left;

    @media (min-width: 768px) {
      font-size: 36px;
    }

  }
}

.custom-checkbox {
  display: inline-block;
  position: relative;
  cursor: pointer;
  width: 23px;
  height: 23px;
  //padding-left: 30px; /* Расстояние между кастомным чекбоксом и текстом */

  input {
    display: none;
  }

  &::before {
    content: "";
    display: inline-block;
    position: absolute;
    left: 0;
    top: 0;
    width: 23px;
    height: 23px;
    border: 2px solid #6489FF;
    background-color: transparent;
    border-radius: 4px;
  }

  &::after {
    content: "";
    display: inline-block;
    position: absolute;
    left: 0;
    top: 0;
    color: #fff;
    font-size: 16px;
    visibility: hidden;
    background-image: url('../images/svg/check.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 15px;
    width: 100%;
    height: 100%;
  }
}

.custom-checkbox {
  &.active {
    &::before {

    }

    &::after {
      visibility: visible;
    }
  }
}

.list-style-none {
  list-style: none;
  padding: 0;
}

.btn {
  display: inline-block;
  color: var(--btn-text);
  background-color: var(--btn-bg);
  border-radius: 65px;
  padding: 12px 30px;
  outline: none;
  box-shadow: none;
  border: none;

  span {
    display: inline-block;
  }

  &:hover, &:active {
    cursor: pointer;
    background-color: var(--btn-bg-hover) !important;
    color: var(--btn-text) !important;
  }
}

input.form-control {
  border-radius: 65px;
  padding: 10px 20px;
  border: none;
}

input.form-control, textarea, label {
  width: 100%;
}

input.form-control, textarea, .btn {
  min-height: 40px;

  @media (min-width: 768px) {
    min-height: 52px;
  }

  @media (min-width: 1200px) {
    font-size: 16px;
  }
}

.accent {
  color: var(--primary-color);
}

.container {
  max-width: 1200px;

  &--wide {
    max-width: 1420px;
  }
}

main {
  display: block;
  overflow: hidden;

  &.isHeaderFixed {
    & > * {
      &:first-child {
        padding-top: 92px;
      }
    }
  }
}

.swiper {
  width: 100%;
  height: 100%;

  .swiper-slide {
    background-color: transparent;
  }
}


.swiper-button-next, .swiper-button-prev {
  //width: 40px;
  //height: 40px;
  //background-color: rgba(219, 52, 31, 0.3);
  top: auto;
  bottom: var(--swiper-navigation-top-offset, 0%);;
  transition: all .1s ease-out;

  @media (min-width: 768px) {
    width: 50px;
    height: 50px;
    bottom: auto;
    top: var(--swiper-navigation-top-offset, 48.5%);
  }

  &::after {
    color: #000;
    font-size: 30px;
    transition: all .1s ease-in;
  }

  &:hover {
    transition: all .1s ease-in;

    &::after {
      color: var(--primary-color);
    }
  }

  &:active {
    color: var(--primary-color);
    //background-color: rgba(84, 235, 255, 0.3);
    transform: scale3d(0.9,0.9,1);
    //border-radius: 4px;
    transition: all .1s ease-in;
  }
}

.swiper-button-next {
  &:hover {
    &::after {
      transform: translateX(3px);
    }
  }

  right: var(--swiper-navigation-sides-offset, 35%);

  @media (min-width: 768px) {
    right: var(--swiper-navigation-sides-offset, 0px);
  }

  @media (min-width: 1650px) {
    right: var(--swiper-navigation-sides-offset, -60px);
  }

  @media (min-width: 1832px) {
    right: var(--swiper-navigation-sides-offset, -60px);
  }
}

.swiper-button-prev {
  &:hover {
    &::after {
      transform: translateX(-3px);
    }
  }

  left: var(--swiper-navigation-sides-offset, 35%);

  @media (min-width: 768px) {
    left: var(--swiper-navigation-sides-offset, 0px);
  }

  @media (min-width: 1650px) {
    left: var(--swiper-navigation-sides-offset, -60px);
  }

  @media (min-width: 1832px) {
    left: var(--swiper-navigation-sides-offset, -60px);
  }
}

.swiper-button-disabled {
  opacity: 0;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

