.announcement-map {
	position: relative;
	z-index: 2;

	.section-title {
		max-width: 90%;
		margin-left: auto;
		margin-right: auto;
	}
}


.announcement-map__wrap {
	padding: 0 25px;

	@media (min-width: 992px) {
		padding: 0 50px;
	}

	@media (min-width: 1200px) {
		padding: 0;
		width: 89vw;
		max-width: 1173px;
		margin: 0 auto;
	}
}

.announcement-map__map {
	position: relative;
	display: flex;
	flex-direction: column;

	iframe {
		width: 100%;
		border-radius: 26px;
		height: 200px;

		@media (min-width: 768px) {
			height: 300px;
		}

		@media (min-width: 992px) {
			height: 400px;
		}
	}
}

.announcement-map__btn {
	width: fit-content;
	margin: 10px auto 0;

	@media (min-width: 768px) {
		margin-top: 0;
		position: absolute;
		z-index: 2;
		top: 50%;
		left: 50%;
		transform: translateX(-50%) translateY(-50%);
	}
}