.all-categories {
	.section-title {
		position: relative;
		z-index: 2;
	}
}

.all-categories__plate {
	border-radius: 26px;
	padding: 30px 15px 30px 15px;
	background: #e0e0e0;
	position: relative;
	overflow: hidden;

	@media (min-width: 576px) {
		background: #f0f0f0;
		padding: 45px 20px 50px 30px;
	}

	@media (min-width: 768px) {
		background: #e9e9e9;
	}

	@media (min-width: 992px) {
		padding: 90px 52px;
	}

	&::before {
		border-radius: 26px;
		position: absolute;
		content: "";
		top: 0;
		right: 0;
		width: 100%;
		height: 100%;
		background-color: #f8f8f8;
		z-index: 2;
		opacity: 0.7;
		pointer-events: none;

		@media (min-width: 768px) {
			top: -25%;
			right: 285px;
			width: 100%;
			height: 150%;
			opacity: 0.8;
			filter: blur(100px);
		}
	}
}

.all-categories__bg-image {
	position: absolute;
	width: 200%;
	height: 100%;
	background-repeat: no-repeat;
	background-position: bottom right;
	background-size: 740px;
	top: 0;
	right: -120px;
	pointer-events: none;

	@media (min-width: 768px) {
		width: 100%;
		background-position: bottom right;
		background-size: contain;
		right: -155px;
	}

	@media (min-width: 1400px) {
		background-size: 1100px;
	}
}

.all-categories__list {
	list-style-type: disc;
	color: var(--footer-text-color);
	max-width: 1110px;
	padding-left: 20px;
	position: relative;

	@media (min-width: 576px) {
		margin-top: 30px;
		column-count: 2;
		column-gap: 60px;
	}

	@media (min-width: 992px) {
		margin-top: 50px;
		column-count: 3;
		column-gap: 60px;
	}

	li {
		break-inside: avoid;
		position: relative;
		z-index: 2;
		font-size: 12px;
		line-height: 1.2;

		@media (min-width: 768px) {
			font-size: 13px;
		}

		@media (min-width: 992px) {
			font-size: 16px;
		}

		&:not(:last-child) {
			margin-bottom: 5px;

			@media (min-width: 768px) {
				margin-bottom: 10px;
			}
		}
	}

	a {
		text-decoration: none;
		color: var(--footer-text-color);
		text-shadow: 0 0 4px rgba(245, 245, 245,1);

		&:hover {
			text-decoration: underline;
			color: var(--primary-color);
		}
	}
}

.all-categories__buttons {
	margin-top: 20px;
	position: relative;
	display: flex;
	flex-wrap: wrap;
	gap: 10px;
	z-index: 2;

	@media (min-width: 576px) {
		margin-top: 30px;
	}

	@media (min-width: 768px) {
		gap: 36px;
	}

	.btn {
		width: 100%;
		text-align: left;
		padding: 5px 20px;

		@media (min-width: 470px) {
			width: fit-content;
		}

		svg {
			max-width: 24px;

			@media (max-width: 359px) {
				max-width: 26px;
			}
		}

		@media (min-width: 576px) {
			width: fit-content;
		}

		span {
			display: inline-block;
			padding-left: 15px;

			@media (max-width: 359px) {
				padding-left: 10px;
			}
		}
	}
}